import React from 'react';
import {makeStyles} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import Icon from "@material-ui/core/Icon";
import Container from "@material-ui/core/Container";

const useStyles = makeStyles(theme => ({
    footer: {
        bottom: 0,
        backgroundColor: theme.palette.grey[300],
        color: theme.palette.text.secondary,
        position: "fixed"
    },
    disclaimer: {
        backgroundColor: theme.palette.grey[200]
    },
    copyright: {
        padding: 6,
        fontSize: 10,
    }
}));

const Footer = () => {
    const classes = useStyles();

    return (
        <footer className={classes.footer}>
            <div className={classes.disclaimer}>
                <Container>
                    <Typography variant={"body2"} style={{padding: "12px 0 12px 0"}}>
                        Sollten Sie Fragen haben wenden Sie sich bitte an <a href={"mailto:tganzhorn@gmail.com"}>tganzhorn@gmail.com</a>.
                    </Typography>
                </Container>
            </div>
            <Typography variant={"body2"} className={classes.copyright}>
                <Icon style={{fontSize: 8}}>copyright</Icon>Tobias Ganzhorn, 2019
            </Typography>
        </footer>
    );
};

export default Footer;
